* {
  padding: 0;
  margin: 0;
  // box-sizing: border-box;
}
.App {
  text-align: left;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;

  /* padding-right: 15px;
  padding-left: 15px; */
  margin-right: auto;
  margin-left: auto;
  @media (min-width: 576px) {
    max-width: 540px;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 1200px) {
    max-width: 1140px;
  }
}
